"use client"

import { useEffect, useState } from "react"
import { createClient } from "@/lib/supabase/client"
import { Card, CardContent } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Plus, LogOut, Loader2 } from "lucide-react"
import { StudyCard } from "@/components/study-card"
import { ProgressDashboard } from "@/components/progress-dashboard"
import { ThemeToggle } from "@/components/theme-toggle"
import { AddWordDialog } from "@/components/add-word-dialog"
import { useRouter } from "next/navigation"

type Word = {
  id: string
  original: string
  translation: string
  type: string
  tag: string
  easiness_factor: number
  interval: number
  repetitions: number
  next_review: string
}

export default function Dashboard() {
  const [words, setWords] = useState<Word[]>([])
  const [loading, setLoading] = useState(true)
  const [userName, setUserName] = useState("")
  const [showAddDialog, setShowAddDialog] = useState(false)
  const supabase = createClient()
  const router = useRouter()

  useEffect(() => {
    const fetchData = async () => {
      const {
        data: { user },
      } = await supabase.auth.getUser()
      if (!user) {
        router.push("/login")
        return
      }

      setUserName(user.user_metadata?.full_name || user.email?.split("@")[0] || "Friend")

      const { data: wordsData, error } = await supabase
        .from("words")
        .select("*")
        .lte("next_review", new Date().toISOString()) // Only fetch due words
        .order("next_review", { ascending: true })

      if (wordsData) {
        setWords(wordsData)
      }
      setLoading(false)
    }

    fetchData()
  }, [router, supabase])

  const handleLogout = async () => {
    await supabase.auth.signOut()
    router.push("/login")
  }

  const handleWordAdded = (newWord: Word) => {
    // If the new word is due immediately (which it is by default), add it to current session
    if (new Date(newWord.next_review) <= new Date()) {
      setWords((prev) => [...prev, newWord])
    }
    setShowAddDialog(false)
  }

  const handleWordReviewed = async (id: string, quality: number) => {
    // Calculate new SM-2 values locally or via an API?
    // For simplicity, we'll do it client-side here and update Supabase
    const word = words.find((w) => w.id === id)
    if (!word) return

    // Basic SM-2 implementation
    let { easiness_factor, interval, repetitions } = word

    if (quality >= 3) {
      if (repetitions === 0) interval = 1
      else if (repetitions === 1) interval = 6
      else interval = Math.round(interval * easiness_factor)

      repetitions += 1
      easiness_factor = easiness_factor + (0.1 - (5 - quality) * (0.08 + (5 - quality) * 0.02))
      if (easiness_factor < 1.3) easiness_factor = 1.3
    } else {
      repetitions = 0
      interval = 1
    }

    const next_review = new Date()
    next_review.setDate(next_review.getDate() + interval)

    // Optimistic UI update
    setWords((prev) => prev.filter((w) => w.id !== id))

    // Update DB
    await supabase
      .from("words")
      .update({
        easiness_factor,
        interval,
        repetitions,
        next_review: next_review.toISOString(),
      })
      .eq("id", id)
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-background pb-20">
      {/* Header */}
      <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
        <div className="container flex h-14 items-center justify-between">
          <div className="font-bold text-lg">Vocabulary App</div>
          <div className="flex items-center gap-2">
            <ThemeToggle />
            <Button variant="ghost" size="icon" onClick={handleLogout}>
              <LogOut className="h-5 w-5" />
              <span className="sr-only">Logout</span>
            </Button>
          </div>
        </div>
      </header>

      <main className="container py-6 space-y-8">
        <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
          <div>
            <h1 className="text-3xl font-bold tracking-tight">Hi, {userName}!</h1>
            <p className="text-muted-foreground">You have {words.length} words to review today.</p>
          </div>
          <Button onClick={() => setShowAddDialog(true)}>
            <Plus className="mr-2 h-4 w-4" />
            Add New Word
          </Button>
        </div>

        <div className="grid gap-6 md:grid-cols-2 lg:grid-cols-7">
          {/* Main Study Area */}
          <div className="md:col-span-2 lg:col-span-5 space-y-6">
            {words.length > 0 ? (
              <div className="max-w-xl mx-auto mt-8">
                <StudyCard word={words[0]} onResult={(quality) => handleWordReviewed(words[0].id, quality)} />
              </div>
            ) : (
              <Card className="text-center py-12">
                <CardContent>
                  <div className="text-2xl font-semibold mb-2">All caught up! 🎉</div>
                  <p className="text-muted-foreground mb-6">
                    You've reviewed all your words for now. Come back later or add new words.
                  </p>
                  <Button onClick={() => setShowAddDialog(true)}>Add More Words</Button>
                </CardContent>
              </Card>
            )}
          </div>

          {/* Sidebar / Stats */}
          <div className="md:col-span-2 space-y-6">
            <ProgressDashboard />
          </div>
        </div>
      </main>

      <AddWordDialog open={showAddDialog} onOpenChange={setShowAddDialog} onWordAdded={handleWordAdded} />
    </div>
  )
}
