"use client"

import { useState } from "react"
import { Card } from "@/components/ui/card"
import { Badge } from "@/components/ui/badge"
import { Button } from "@/components/ui/button"
import type { VocabularyWord } from "@/lib/vocabulary"
import type { CardProgress } from "@/lib/spaced-repetition"
import { ReviewQuality } from "@/lib/spaced-repetition"
import { cn } from "@/lib/utils"
import { RotateCcw, Zap, Clock, CheckCircle2 } from "lucide-react"

interface StudyCardProps {
  word: VocabularyWord
  progress: CardProgress
  onReview: (quality: ReviewQuality) => void
  currentIndex: number
  totalCards: number
}

export function StudyCard({ word, progress, onReview, currentIndex, totalCards }: StudyCardProps) {
  const [isFlipped, setIsFlipped] = useState(false)

  const handleFlip = () => {
    setIsFlipped(!isFlipped)
  }

  const handleReview = (quality: ReviewQuality) => {
    onReview(quality)
    setIsFlipped(false)
  }

  const getStatusColor = () => {
    switch (progress.status) {
      case "new":
        return "bg-blue-500/10 text-blue-500 border-blue-500/20"
      case "learning":
        return "bg-yellow-500/10 text-yellow-500 border-yellow-500/20"
      case "review":
        return "bg-green-500/10 text-green-500 border-green-500/20"
      case "mastered":
        return "bg-purple-500/10 text-purple-500 border-purple-500/20"
    }
  }

  const getStatusText = () => {
    switch (progress.status) {
      case "new":
        return "Новое"
      case "learning":
        return "Изучаю"
      case "review":
        return "Повтор"
      case "mastered":
        return "Выучено"
    }
  }

  return (
    <div className="w-full max-w-2xl mx-auto">
      {/* Header */}
      <div className="mb-6 flex items-center justify-between">
        <div className="text-sm text-muted-foreground font-medium">
          Карточка {currentIndex + 1} / {totalCards}
        </div>
        <div className="flex gap-2">
          <Badge className={cn("text-xs", getStatusColor())}>{getStatusText()}</Badge>
          <Badge variant="secondary" className="text-xs">
            {word.type}
          </Badge>
          <Badge variant="outline" className="text-xs">
            {word.tag}
          </Badge>
        </div>
      </div>

      {/* Flashcard */}
      <div className="relative h-[420px] cursor-pointer perspective-1000" onClick={handleFlip}>
        <div
          className={cn(
            "absolute inset-0 transition-transform duration-500 transform-style-3d",
            isFlipped ? "rotate-y-180" : "",
          )}
        >
          {/* Front */}
          <Card
            className={cn(
              "absolute inset-0 backface-hidden flex flex-col items-center justify-center p-8",
              "bg-gradient-to-br from-primary/5 via-background to-secondary/5",
              "border-2 hover:shadow-2xl transition-all hover:scale-[1.02]",
            )}
          >
            <div className="text-center space-y-6">
              <div className="text-7xl font-bold text-foreground tracking-tight">{word.word}</div>
              <div className="flex items-center gap-2 text-sm text-muted-foreground">
                <RotateCcw className="h-4 w-4" />
                <span>Нажмите, чтобы увидеть перевод</span>
              </div>
            </div>
          </Card>

          {/* Back */}
          <Card
            className={cn(
              "absolute inset-0 backface-hidden rotate-y-180 flex flex-col items-center justify-center p-8",
              "bg-gradient-to-br from-secondary/5 via-background to-accent/5",
              "border-2 hover:shadow-2xl transition-all",
            )}
          >
            <div className="text-center space-y-4">
              <div className="text-6xl font-bold text-foreground">{word.translation}</div>
              <div className="text-2xl text-muted-foreground font-medium">{word.word}</div>
              <div className="pt-4 text-sm text-muted-foreground/60">
                Оцените, насколько хорошо вы помните это слово
              </div>
            </div>
          </Card>
        </div>
      </div>

      {/* Review Buttons */}
      {isFlipped && (
        <div className="mt-8 grid grid-cols-4 gap-3">
          <Button
            onClick={(e) => {
              e.stopPropagation()
              handleReview(ReviewQuality.Again)
            }}
            variant="outline"
            className="flex-col h-auto py-4 gap-2 border-2 hover:border-red-500 hover:bg-red-500/10"
          >
            <RotateCcw className="h-5 w-5 text-red-500" />
            <span className="text-xs font-medium">Снова</span>
            <span className="text-[10px] text-muted-foreground">10 мин</span>
          </Button>

          <Button
            onClick={(e) => {
              e.stopPropagation()
              handleReview(ReviewQuality.Hard)
            }}
            variant="outline"
            className="flex-col h-auto py-4 gap-2 border-2 hover:border-orange-500 hover:bg-orange-500/10"
          >
            <Clock className="h-5 w-5 text-orange-500" />
            <span className="text-xs font-medium">Трудно</span>
            <span className="text-[10px] text-muted-foreground">1 день</span>
          </Button>

          <Button
            onClick={(e) => {
              e.stopPropagation()
              handleReview(ReviewQuality.Good)
            }}
            variant="outline"
            className="flex-col h-auto py-4 gap-2 border-2 hover:border-green-500 hover:bg-green-500/10"
          >
            <CheckCircle2 className="h-5 w-5 text-green-500" />
            <span className="text-xs font-medium">Хорошо</span>
            <span className="text-[10px] text-muted-foreground">
              {progress.repetitions === 0 ? "1 день" : "6 дней"}
            </span>
          </Button>

          <Button
            onClick={(e) => {
              e.stopPropagation()
              handleReview(ReviewQuality.Easy)
            }}
            variant="outline"
            className="flex-col h-auto py-4 gap-2 border-2 hover:border-blue-500 hover:bg-blue-500/10"
          >
            <Zap className="h-5 w-5 text-blue-500" />
            <span className="text-xs font-medium">Легко</span>
            <span className="text-[10px] text-muted-foreground">
              {Math.round(progress.interval * progress.easeFactor)} дн
            </span>
          </Button>
        </div>
      )}

      <style jsx>{`
        .perspective-1000 {
          perspective: 1000px;
        }
        .transform-style-3d {
          transform-style: preserve-3d;
        }
        .backface-hidden {
          backface-visibility: hidden;
        }
        .rotate-y-180 {
          transform: rotateY(180deg);
        }
      `}</style>
    </div>
  )
}
