"use client"

import type React from "react"

import { useState } from "react"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogFooter } from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { createClient } from "@/lib/supabase/client"
import { Loader2 } from "lucide-react"

interface AddWordDialogProps {
  open: boolean
  onOpenChange: (open: boolean) => void
  onWordAdded: (word: any) => void
}

export function AddWordDialog({ open, onOpenChange, onWordAdded }: AddWordDialogProps) {
  const [original, setOriginal] = useState("")
  const [translation, setTranslation] = useState("")
  const [type, setType] = useState("noun")
  const [tag, setTag] = useState("general")
  const [loading, setLoading] = useState(false)
  const supabase = createClient()

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    setLoading(true)

    const {
      data: { user },
    } = await supabase.auth.getUser()

    if (!user) return

    const newWord = {
      user_id: user.id,
      original,
      translation,
      type,
      tag,
      next_review: new Date().toISOString(), // Due immediately
      easiness_factor: 2.5,
      interval: 0,
      repetitions: 0,
    }

    const { data, error } = await supabase.from("words").insert([newWord]).select().single()

    setLoading(false)

    if (!error && data) {
      onWordAdded(data)
      setOriginal("")
      setTranslation("")
      onOpenChange(false)
    }
  }

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent>
        <DialogHeader>
          <DialogTitle>Add New Word</DialogTitle>
        </DialogHeader>
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label>Word (English)</Label>
              <Input value={original} onChange={(e) => setOriginal(e.target.value)} placeholder="Apple" required />
            </div>
            <div className="space-y-2">
              <Label>Translation</Label>
              <Input
                value={translation}
                onChange={(e) => setTranslation(e.target.value)}
                placeholder="Яблоко"
                required
              />
            </div>
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label>Type</Label>
              <Select value={type} onValueChange={setType}>
                <SelectTrigger>
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="noun">Noun (Сущ.)</SelectItem>
                  <SelectItem value="verb">Verb (Глагол)</SelectItem>
                  <SelectItem value="adjective">Adjective (Прил.)</SelectItem>
                  <SelectItem value="phrase">Phrase (Фраза)</SelectItem>
                </SelectContent>
              </Select>
            </div>
            <div className="space-y-2">
              <Label>Tag</Label>
              <Input value={tag} onChange={(e) => setTag(e.target.value)} placeholder="Kitchen, Travel..." />
            </div>
          </div>

          <DialogFooter>
            <Button type="submit" disabled={loading}>
              {loading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
              Save Word
            </Button>
          </DialogFooter>
        </form>
      </DialogContent>
    </Dialog>
  )
}
